@import url(https://fonts.googleapis.com/css?family=Bungee);


body {
  margin: 0;
  padding: 0;
  font-family: -apple-system, BlinkMacSystemFont, "Segoe UI", "Roboto", "Oxygen",
    "Ubuntu", "Cantarell", "Fira Sans", "Droid Sans", "Helvetica Neue",
    sans-serif;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
}

code {
  font-family: source-code-pro, Menlo, Monaco, Consolas, "Courier New",
    monospace;
}

button {
	border: 1px solid lightgray;
	background-color: transparent;
	padding: 10px;
	border-radius: 4px;
	margin: 1rem 5px;
	color: #87b9db;
}

button:hover {
	cursor: pointer;
}

button:focus {
	outline: 0;
}

.card {
	margin: 4rem .8rem 0 .8rem;
	border: 1px solid black;
	border-radius: 4px;
	box-shadow: -2px -1px 15px 0px rgba(133, 133, 133, 1);
	box-shadow: -2px -1px 15px 0px rgba(133, 133, 133, 1);
	display: flex;
	flex-direction: column;
	justify-content: center;
	background-color: #2a2d3c;
}

.card-section-one {
	display: flex;
	flex-direction: column;
	justify-content: center;
	height: 350px;
	background-color: #2a2d3c;
}

.card-section-two {
	background-color: #2a2d3c;
	height: 100%;
}

.media {
	list-style-type: none;
}

.media i {
	font-size: 2rem;
	color: #70aee7;
	margin: 10px;
	opacity: 0.9;
}

.source-code {
	color: #70aee7;
	font-size: 1rem;
}

.project-trainers {
	width: 220px;
	border-radius: 5px;
}

p {
	overflow: hidden;;
	color: #70aee7;
	font-size: 1rem;
	padding: 0 10px 0 10px;
	margin: 10px;
}

@media (min-width: 768px) {
	.card {
		max-width: 960px;
		height: 400px;
		margin: 3rem auto;
		flex-direction: row;
	}

	.card-section-one {
		width: 60%;
		height: auto;
	}
	.card-section-two {
		width: 40%;
		display: flex;
		flex-direction: column;
		justify-content: center;
		align-items: center;
	}
	
	.project-trainers {
		width: 300px;
	}
}

* {
	margin: 0;
	padding: 0;
}

.App {
	text-align: center;
}

.space {
	margin-right: 10px;
}

.num-counter {
	color: #e37880;
}

h2 {
	padding: 1rem;
	color: #bb9eda;
}

.testo {
	color: #e37880;
}

nav {
	height: 60px;
	padding: 10px;
	border: 1px solid rgb(124, 187, 255);
	background-color: rgb(124, 187, 255);
}

.main-logo {
	display: flex;
	justify-content: center;
	align-items: center;
}

.main-logo img {
	width: 60px;
	-webkit-animation: App-logo-spin infinite 20s linear;
	        animation: App-logo-spin infinite 20s linear;
}

@-webkit-keyframes App-logo-spin {
	from {
		-webkit-transform: rotate(0deg);
		        transform: rotate(0deg);
	}
	to {
		-webkit-transform: rotate(360deg);
		        transform: rotate(360deg);
	}
}

@keyframes App-logo-spin {
	from {
		-webkit-transform: rotate(0deg);
		        transform: rotate(0deg);
	}
	to {
		-webkit-transform: rotate(360deg);
		        transform: rotate(360deg);
	}
}

.logo-text {
	text-align: center;
	font-family: 'Bungee';
}

@media only screen and (min-width: 600px) {
	.logo-text {
		font-size: 1.8rem;
	}
}

